.app{
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        background-color: limegreen;
        position: relative;
        top: 70px;
        // top: 50px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width:none; //for firefox
        &::-webkit-scrollbar{  //for all other browsers
            display: none;
        }

        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align:start ;
        }
    }
}
